import { graphql } from 'gatsby';
import { Layout } from '../components/Layout/Layout';
import React, { useMemo } from 'react';
import { ComboboxData, Stack, Title } from '@mantine/core';
import { DangerRatingFilters } from '../components/DangerRatingFilters';
import { DangerRatingChart } from '../components/DangerRatingChart';
import { useDangerRatingFilters } from '../hooks/useDangerRatingFilters';
import { DangerRatingDriverDataItem } from '../types';
import { DangerRatingTable } from '../components/DangerRatingTable/DangerRatingTable';

type PageData = {
  allWcrDr2Csv: {
    nodes: DangerRatingDriverDataItem[];
  };
};

export type DangerRatingFilterState = {
  gt100: boolean;
  active: boolean;
  driverNames: string[];
  sortBy: keyof DangerRatingDriverDataItem;
  sortDir: 'asc' | 'desc';
};

export type DangerRatingFilterStateSetter = (partialFilter: Partial<DangerRatingFilterState>) => void;
export type DangerRatingFilterSortHandler = (field: keyof DangerRatingDriverDataItem) => void;

const calculateDr = ({ ControlLosses, CarContacts, Contacts, Laps }: DangerRatingDriverDataItem) => {
  const rawValue = (ControlLosses * 2 + (CarContacts + Contacts) * 4) / Laps;

  if (isNaN(rawValue)) return 0;

  return Number(rawValue.toFixed(3));
};
const calculateSr = ({ Offtracks, ControlLosses, CarContacts, Contacts, Laps }: DangerRatingDriverDataItem) => {
  const rawValue = (Offtracks + ControlLosses * 2 + (CarContacts + Contacts) * 4) / Laps;

  if (isNaN(rawValue)) return 0;

  return Number(rawValue.toFixed(3));
};

const DangerRating: React.FC<{
  data: PageData;
}> = ({ data }) => {
  const driverData = useMemo(
    () =>
      data.allWcrDr2Csv.nodes.map((row) => ({
        ...row,
        SR: calculateSr(row),
        DR: calculateDr(row),
      })),
    [data],
  );

  const { filters, filteredDriverData, handleSetFilters, handleSortField } = useDangerRatingFilters(driverData);

  const driverNameFilterOpts: ComboboxData = driverData.map((driver) => ({
    label: driver.Driver,
    value: driver.Driver,
  }));

  return (
    <Layout>
      <Stack>
        <Title order={1}>Danger Ratings</Title>
        <DangerRatingChart driverData={filteredDriverData} />
        <DangerRatingFilters filters={filters} setFilters={handleSetFilters} driverNames={driverNameFilterOpts} />
        <DangerRatingTable filters={filters} driverData={filteredDriverData} onSortField={handleSortField} />
      </Stack>
    </Layout>
  );
};

export const query = graphql`
  query {
    allWcrDr2Csv(sort: { Driver: ASC }) {
      nodes {
        Driver
        ControlLosses
        Active
        id
        Races
        Offtracks
        Laps
        Inc
        CarContacts
        BlackFlags
        Contacts
      }
    }
  }
`;

export default DangerRating;
