import { Table } from '@mantine/core';
import React from 'react';
import { SortableTableHeader } from '../SortableTableHeader';
import { DangerRatingFilterSortHandler, DangerRatingFilterState } from '../../hooks/useDangerRatingFilters';
import { DangerRatingDriverDataItem } from '../../types';

export const DangerRatingTable: React.FC<{
  filters: DangerRatingFilterState;
  driverData: DangerRatingDriverDataItem[];
  onSortField: DangerRatingFilterSortHandler;
}> = ({ filters, driverData, onSortField }) => {
  const rows = driverData.map((row) => (
    <Table.Tr key={row.id}>
      <Table.Td>{row.Driver}</Table.Td>
      <Table.Td>{row.Active ? 'Active' : 'Inactive'}</Table.Td>
      <Table.Td>{row.DR.toFixed(3)}</Table.Td>
      <Table.Td>{row.SR.toFixed(3)}</Table.Td>
      <Table.Td>{row.Races}</Table.Td>
      <Table.Td>{row.Laps}</Table.Td>
      <Table.Td>{row.Inc}</Table.Td>
      <Table.Td>{row.Offtracks}</Table.Td>
      <Table.Td>{row.ControlLosses}</Table.Td>
      <Table.Td>{row.CarContacts}</Table.Td>
      <Table.Td>{row.Contacts}</Table.Td>
      <Table.Td>{row.BlackFlags}</Table.Td>
    </Table.Tr>
  ));

  return (
    <Table>
      <Table.Thead>
        <Table.Tr>
          <SortableTableHeader
            active={filters.sortBy === 'Driver'}
            variant='string'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Driver')}
          >
            Driver Name
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'Active'}
            variant='string'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Active')}
          >
            Status
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'DR'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('DR')}
          >
            DR
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'SR'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('SR')}
          >
            SR
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'Races'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Races')}
          >
            Races
          </SortableTableHeader>

          <SortableTableHeader
            active={filters.sortBy === 'Laps'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Laps')}
          >
            Laps
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'Inc'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Inc')}
          >
            Incidents
          </SortableTableHeader>

          <SortableTableHeader
            active={filters.sortBy === 'Offtracks'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Offtracks')}
          >
            Off Tracks
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'ControlLosses'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('ControlLosses')}
          >
            Control Losses
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'CarContacts'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('CarContacts')}
          >
            Car Contacts
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'Contacts'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('Contacts')}
          >
            Contacts
          </SortableTableHeader>
          <SortableTableHeader
            active={filters.sortBy === 'BlackFlags'}
            variant='number'
            sortDir={filters.sortDir}
            onClick={() => onSortField('BlackFlags')}
          >
            Black Flags
          </SortableTableHeader>
        </Table.Tr>
      </Table.Thead>
      <Table.Tbody>{rows}</Table.Tbody>
    </Table>
  );
};
