import { Table, UnstyledButton } from '@mantine/core';
import {
  Icon,
  IconProps,
  IconSortAscendingLetters,
  IconSortAscendingNumbers,
  IconSortDescendingLetters,
  IconSortDescendingNumbers,
} from '@tabler/icons-react';
import React from 'react';
import * as classes from './SortableTableHeader.module.css';

export const SortableTableHeader: React.FC<
  React.PropsWithChildren<{
    active: boolean;
    sortDir: 'asc' | 'desc';
    variant?: 'string' | 'number';
    onClick: () => void;
  }>
> = ({ active, sortDir, variant = 'string', onClick, children }) => {
  const variants: Record<'string' | 'number', React.ForwardRefExoticComponent<IconProps & React.RefAttributes<Icon>>> = {
    string: sortDir === 'asc' ? IconSortAscendingLetters : IconSortDescendingLetters,
    number: sortDir === 'asc' ? IconSortAscendingNumbers : IconSortDescendingNumbers,
  };

  const SortIndicatorIcon = variants[variant];

  return (
    <Table.Th>
      <UnstyledButton onClick={onClick} className={classes.button}>
        {children}
        <span className={classes.iconContainer}>{active && <SortIndicatorIcon className={classes.icon} />}</span>
      </UnstyledButton>
    </Table.Th>
  );
};
