import { BarChart } from '@mantine/charts';
import React from 'react';
import { DangerRatingDriverDataItem } from '../types';

const getDangerRatingDistributionChartData = (drivers: DangerRatingDriverDataItem[]): { drRange: string; Drivers: number }[] => {
  const chartDataLabels = {
    0: '0.0 - 0.1',
    1: '0.1 - 0.2',
    2: '0.2 - 0.3',
    3: '0.3 - 0.4',
    4: '0.4 - 0.5',
    5: '0.5 - 0.6',
    6: '0.6 - 0.7',
    7: '0.7 - 0.8',
    8: '0.8 - 0.9',
    9: '0.9 - 1.0+',
  };

  const chartData = drivers.reduce(
    (acc, row) => {
      const roundedVal = Math.floor(row.DR * 10);

      const minMax = Math.max(0, Math.min(roundedVal, 9));

      const label = chartDataLabels[minMax as keyof typeof chartDataLabels];

      if (!acc[label]) {
        acc[label] = 0;
      }

      acc[label] += 1;

      return acc;
    },
    {} as Record<string, number>,
  );

  return Object.keys(chartData)
    .map((range) => ({
      drRange: range,
      Drivers: chartData[range],
    }))
    .sort((a, b) => a.drRange.localeCompare(b.drRange));
};

export const DangerRatingChart: React.FC<{ driverData: DangerRatingDriverDataItem[] }> = ({ driverData }) => {
  const chartData = getDangerRatingDistributionChartData(driverData);

  return (
    <BarChart
      h={130}
      mb='md'
      mt='md'
      data={chartData}
      dataKey='drRange'
      series={[{ name: 'Drivers' }]}
      withBarValueLabel
      valueFormatter={(value) => new Intl.NumberFormat('en-US').format(value)}
      tickLine='none'
      gridAxis='none'
      withYAxis={false}
    />
  );
};
