import { Checkbox, ComboboxData, Group, MultiSelect } from '@mantine/core';
import React from 'react';
import { DangerRatingFilterState, DangerRatingFilterStateSetter } from '../../pages/danger-rating';
import * as classes from './DangerRatingFilters.module.css';

export const DangerRatingFilters: React.FC<{
  filters: DangerRatingFilterState;
  setFilters: DangerRatingFilterStateSetter;
  driverNames: ComboboxData;
}> = ({ filters, setFilters, driverNames }) => {
  return (
    <div className={classes.filtersContainer}>
      <div>
        <div className={classes.filterTitle}>Filters:</div>
        <Group>
          <Checkbox
            label='Drivers with > 100 laps'
            value='gt100'
            checked={filters.gt100}
            onChange={(e) =>
              setFilters({
                gt100: e.target.checked,
              })
            }
          />
          <Checkbox
            label='Active drivers'
            value='active'
            checked={filters.active}
            onChange={(e) =>
              setFilters({
                active: e.target.checked,
              })
            }
          />
        </Group>
      </div>
      <div className={classes.filtersBottomRow}>
        <MultiSelect
          label='Filter by driver(s)'
          description='Select one or more drivers'
          data={driverNames}
          searchable
          clearable
          value={filters.driverNames}
          onChange={(selected) => setFilters({ driverNames: selected })}
        />
      </div>
    </div>
  );
};
