import { useCallback, useMemo, useState } from 'react';
import { DangerRatingDriverDataItem } from '../types';

export type DangerRatingFilterState = {
  gt100: boolean;
  active: boolean;
  driverNames: string[];
  sortBy: keyof DangerRatingDriverDataItem;
  sortDir: 'asc' | 'desc';
};

export type DangerRatingFilterStateSetter = (partialFilter: Partial<DangerRatingFilterState>) => void;
export type DangerRatingFilterSortHandler = (field: keyof DangerRatingDriverDataItem) => void;

export const useDangerRatingFilters = (
  driverData: DangerRatingDriverDataItem[],
): {
  filters: DangerRatingFilterState;
  filteredDriverData: DangerRatingDriverDataItem[];
  handleSetFilters: DangerRatingFilterStateSetter;
  handleSortField: DangerRatingFilterSortHandler;
} => {
  const [filters, _setFilters] = useState<DangerRatingFilterState>({
    gt100: false,
    active: false,
    driverNames: [],
    sortBy: 'Driver',
    sortDir: 'asc',
  });

  const handleSetFilters = useCallback((partialFilter: Partial<typeof filters>) => {
    _setFilters((oldFilters) => ({
      ...oldFilters,
      ...partialFilter,
    }));
  }, []);

  const handleSortField = useCallback((field: keyof DangerRatingDriverDataItem) => {
    _setFilters((oldFilters) => {
      if (field === oldFilters.sortBy) {
        return {
          ...oldFilters,
          sortDir: oldFilters.sortDir === 'asc' ? 'desc' : 'asc',
        };
      } else {
        return {
          ...oldFilters,
          sortBy: field,
          sortDir: 'asc',
        };
      }
    });
  }, []);

  const filteredDriverData = useMemo(() => {
    const sortAsc = (a: DangerRatingDriverDataItem, b: DangerRatingDriverDataItem) => {
      if (a[filters.sortBy] > b[filters.sortBy]) {
        return 1;
      } else if (a[filters.sortBy] < b[filters.sortBy]) {
        return -1;
      }

      return 0;
    };

    const sortDesc = (a: DangerRatingDriverDataItem, b: DangerRatingDriverDataItem) => {
      if (b[filters.sortBy] > a[filters.sortBy]) {
        return 1;
      } else if (b[filters.sortBy] < a[filters.sortBy]) {
        return -1;
      }

      return 0;
    };

    return driverData
      .filter((row) => (filters.active ? row.Active : true))
      .filter((row) => (filters.gt100 ? row.Laps > 100 : true))
      .filter((row) => (filters.driverNames.length > 0 ? filters.driverNames.includes(row.Driver) : true))
      .sort(filters.sortDir === 'asc' ? sortAsc : sortDesc);
  }, [driverData, filters]);

  return {
    filters,
    filteredDriverData,
    handleSetFilters,
    handleSortField,
  };
};
